ore {\n      position: absolute;\n      content: '';\n      bottom: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      border-radius: 0;\n      background: $dark;\n    }\n\n    &::after {\n      position: absolute;\n      content: '';\n      bottom: 0;\n      left: 0;\n      background: #FFF;\n      width: 100%;\n      height: 100%;\n      border-radius: 19px 0 0 0;\n    }\n\n    &--reverse {\n      transform: rotate(-90deg);\n      top: -19px;\n      display: none;\n    }\n\n    @media(max-width: 767px) {\n      display: none;\n    }\n  }\n\n  &__cart-link {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    z-index: 1;\n    left: 0;\n    top: 0;\n  }\n\n  &__cart-amount {\n    color: $dark;\n    padding: 0 12px;\n    font-weight: 600;\n    font-size: 12px;\n  }\n\n  &__cart-ico {\n    fill: $primary;\n    width: 26px;\n    height: 26px;\n  }\n\n  &__nav-ico {\n    fill: $primary;\n    width: 26px;\n    height: 26px;\n    pointer-events: none;\n  }\n\n  &__search {\n    &--mobile {\n      display: none;\n      @media(max-width: 767px) {\n        display: block;\n      }\n    }\n  }\n\n  &__menu {\n    position: absolute;\n    top: 0;\n    left: 50px;\n    bottom: 0;\n    transform: translate(-100%, 0);\n    opacity: 0;\n    pointer-events: none;\n    width: 400px;\n    transition: transform 0.2s, opacity 0.3s;\n    z-index: 300;\n\n    @media(max-width: 767px) {\n      background: $dark;\n      width: 100%;\n      left: 0;\n    }\n  }\n\n  &__menu-close {\n    position: absolute;\n    top: 0;\n    right: -90px;\n    width: 90px;\n    height: 90px;\n    background: $dark;\n    border-radius: 0 20px 20px 0;\n    cursor: pointer;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n\n    #{$root} {\n      &__nav-ico {\n        fill: #FFF;\n      }\n    }\n\n    @media(max-width: 767px) {\n      top: 20px;\n      right: 20px;\n      width: 26px;\n      height: 26px;\n      z-index: 1;\n    }\n\n    &--filter {\n      top: 90px;\n      #{$root} {\n        &__nav-radius--reverse {\n          display: block;\n        }\n      }\n    }\n  }\n\n  &__menu-container {\n    top: 0;\n    left: 0;\n    bottom: 0;\n    position: absolute;\n    width: 100%;\n  }\n\n  &__menu-inner {\n    background: $dark;\n    border-radius: 20px 0 20px 20px;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    overflow: hidden;\n    position: relative;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n  }\n\n  &__menu-scroll {\n    width: calc(100% - 30px);\n    max-height: calc(100vh - 130px);\n    position: relative;\n    overflow: hidden;\n    overflow-y: auto;\n\n    @media(max-width: 767px) {\n      max-height: calc(100vh - 40px);\n    }\n\n    &::-webkit-scrollbar-track {\n      background-color:$dark;\n    }\n\n    &::-webkit-scrollbar-thumb {\n      -webkit-border-radius: 0px;\n      border-radius: 0px;\n      background-color:#5d6074;\n    }\n\n    &::-webkit-scrollbar-thumb:hover{\n      background-color:#5d6074;\n    }\n\n    &::-webkit-resizer{\n      width:3px;\n      height:0px\n    }\n\n    &::-webkit-scrollbar{\n      width: 3px;\n    }\n  }\n\n  &__menu-title {\n    font-weight: bold;\n    font-size: 26px;\n    color: #fff;\n    padding: 45px 40px 40px 40px;\n\n    @media(max-width: 767px) {\n      font-size: 20px;\n      padding: 20px;\n    }\n\n    &--mobile {\n      display: none;\n      @media(max-width: 767px) {\n        display: block;\n      }\n    }\n\n  }\n\n  &__menu-list {\n    &--mobile {\n      display: none;\n      @media(max-width: 767px) {\n        display: block;\n      }\n    }\n  }\n\n  &__menu-item {\n    position: relative;\n    margin: 0 10px 10px 10px;\n\n    &--has-open {\n      #{$root} {\n        &__menu-ico {\n          transform: rotate(90deg);\n        }\n      }\n    }\n\n    &:last-child {\n      margin-bottom: 40px;\n    }\n  }\n\n  &__menu-link {\n    width: 100%;\n    font-weight: 500;\n    font-size: 16px;\n    color: #fff;\n    text-decoration: none;\n    display: block;\n    background: rgba(255, 255, 255, 0.07);\n    border-radius: 7px;\n    padding: 13px 35px 12px 20px;\n    transition: background 0.3s;\n    position: relative;\n    text-align: left;\n\n    @media(max-width: 767px) {\n      font-size: 14px;\n      padding: 10px 20px 10px 15px;\n    }\n\n    &:hover, &:focus {\n      background: $shadow-dark2;\n    }\n  }\n\n  &__menu-ico {\n    position: absolute;\n    right: 20px;\n    top: calc(50% - 7px);\n    fill: #FFF;\n    width: 14px;\n    height: 14px;\n    transition: transform .3s ease;\n    @media(max-width: 767px) {\n      width: 10px;\n      height: 10px;\n      top: calc(50% - 5px);\n      right: 15px;\n    }\n  }\n\n  &__menu-dropdown {\n    height: 0;\n    overflow: hidden;\n    padding-left: 30px;\n    transition: height .3s ease;\n    @media(max-width: 767px) {\n      padding-left: 20px;\n    }\n    #{$root} {\n      &__menu-link {\n        margin-top: 10px;\n        font-size: 15px;\n        padding: 10px 25px 10px 20px;\n        @media(max-width: 767px) {\n          font-size: 13px;\n          padding: 8px 20px 8px 15px;\n        }\n      }\n    }\n  }\n}\n","$primary: #191919;\n$dark: #41445C;\n$dark2: #53566c;\n$shadow-primary: rgba(65, 68, 92, 0.3);\n$shadow-dark2: rgba(248, 248, 248, 0.2);\n$grey: #818181;\n$light: #c4c4c4;"],"sourceRoot":""}]);
